@font-face {
  font-family: "Arial";
  src: url("../fonts/ARIAL.TTF");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Arial";
  src: url("../fonts/ARIALI.TTF");
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: "Georgia";
  src: url("../fonts/GEORGIA.TTF");
  font-weight: normal;
  font-style: normal;
}
* {
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

h1 {
  display: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Georgia";
  font-style: normal;
  font-weight: normal;
}

.btn {
  width: 207px;
  height: 52px;
  border-radius: 100px;
  border: 2px solid #F1CDB3;
  box-sizing: border-box;
  font-family: "Georgia";
  font-size: 17px;
  line-height: 130%;
  letter-spacing: 0.06em;
  color: #292929;
  background: #F1CDB3;
  transition: all ease-out 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
.btn:hover {
  background: #FDDCC4;
  border-color: #FDDCC4;
  cursor: pointer;
  transition: all ease-out 0.2s;
}

.center {
  max-width: 1280px;
  margin: auto;
  padding: 0 40px;
  box-sizing: border-box;
}

/* popup */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: rgba(41, 41, 41, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.close-btn {
  position: absolute;
  width: 52px;
  height: 52px;
  border: 2px solid #F1CDB3;
  box-sizing: border-box;
  border-radius: 100px;
  background-image: url("../images/icons/close-popup.svg");
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
  right: -40px;
  top: -50px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.close-btn:hover {
  background-color: #FDDCC4;
  transition: all 0.3s ease-in-out;
}

.popup-wrapper {
  display: flex;
  width: 900px;
  min-height: 500px;
  height: auto;
  box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
  background: #FAFAFA;
  border-radius: 9px;
  position: relative;
}

.popup-img {
  width: 500px;
  height: 500px;
  flex-shrink: 0;
}
.popup-img img {
  width: 100%;
  border-radius: 9px 0px 0px 9px;
}

.popup-text {
  padding: 50px 20px 50px 30px;
}

.popup-name {
  font-weight: 400;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 0.06em;
  margin-bottom: 10px;
}

.popup-type {
  font-weight: 400;
  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;
  margin-bottom: 40px;
}

.popup-description {
  font-weight: 400;
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
  margin-bottom: 40px;
}

.popup-list {
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
  font-weight: 400;
}
.popup-list li {
  margin-bottom: 10px;
  position: relative;
  padding-left: 14px;
}
.popup-list li::before {
  position: absolute;
  left: 0;
  top: 7px;
  content: "";
  width: 4px;
  height: 4px;
  background: #F1CDB3;
  border-radius: 50%;
}

@media (max-width: 1279px) {
  .popup-wrapper {
    width: 630px;
    min-height: 350px;
  }

  .popup-img {
    height: 350px;
    width: 350px;
  }

  .popup-text {
    padding: 10px;
    overflow-y: scroll;
  }

  .popup-type {
    margin-bottom: 20px;
  }

  .popup-description {
    margin-bottom: 20px;
    font-size: 13px;
  }

  .popup-list li {
    margin-bottom: 5px;
  }
}
@media (max-width: 767px) {
  .popup-wrapper {
    width: 240px;
    min-height: 341px;
    margin-right: 20px;
  }

  .popup-img {
    display: none;
  }

  .popup-name {
    text-align: center;
  }

  .popup-type {
    text-align: center;
    font-size: 20px;
    line-height: 115%;
  }

  .popup-description {
    margin-bottom: 24px;
  }
}
footer {
  background: url("../images/noise_transparent2x.png"), radial-gradient(110.67% 538.64% at 5.73% 50%, #513D2F 0%, #1A1A1C 100%), #211F20;
  padding-top: 37px;
  height: 350px;
  box-sizing: border-box;
  overflow: hidden;
}
footer .footer_wrapper {
  display: flex;
  justify-content: space-between;
}
footer .footer_contacts {
  max-width: 300px;
}
footer .footer_location {
  max-width: 322px;
}
footer .footer_questions, footer .footer_visit {
  display: flex;
  flex-direction: column;
  font-weight: normal;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 0.06em;
  color: #ffffff;
  margin-top: 18px;
}
footer .footer_email {
  margin: 38px 0 40px;
}
footer .footer_address:first-of-type {
  margin: 32px 0 35px;
}
footer .footer_email,
footer .footer_phone,
footer .footer_address {
  font-weight: normal;
  font-size: 20px;
  line-height: 110%;
  letter-spacing: 0.06em;
  color: #F1CDB3;
  text-decoration: none;
  display: flex;
  align-items: center;
}
footer .footer_email img,
footer .footer_phone img,
footer .footer_address img {
  margin-right: 20px;
}
footer .footer_photo_puppy {
  width: 300px;
  height: 315px;
}

@media (max-width: 1279px) {
  footer {
    height: auto;
    padding-top: 30px;
  }

  footer .footer_wrapper {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  footer .footer_contacts, footer .footer_location {
    margin-right: 0;
    max-width: 45%;
    box-sizing: border-box;
  }

  footer .footer_contacts {
    padding-left: 16px;
  }

  footer .footer_questions, footer .footer_visit {
    margin-top: 0;
  }

  footer .footer_photo_puppy {
    margin: 65px auto 0;
  }
}
@media (max-width: 767px) {
  footer .footer_wrapper {
    flex-direction: column;
    align-items: center;
  }

  footer .footer_questions, footer .footer_visit {
    text-align: center;
    font-size: 25px;
    line-height: 130%;
    letter-spacing: 0.06em;
  }

  footer .footer_contacts,
footer .footer_location {
    max-width: 100%;
    padding: 0;
  }

  footer .footer_photo_puppy {
    margin-top: 0;
    width: 260px;
    height: 269px;
  }

  footer .footer_photo_puppy img {
    width: 260px;
    height: 269px;
  }

  footer .footer_email, footer .footer_phone {
    margin: 40px auto;
    justify-content: center;
  }

  footer .footer_visit {
    max-width: 95%;
    margin: 0 auto;
  }

  footer .footer_address, footer .footer_address:first-of-type {
    margin: 40px auto;
    width: 300px;
  }

  footer .footer_contacts {
    margin-right: 0;
  }
}